import React from 'react';
import { Tab, Tabs, Typography } from "@material-ui/core";
import TideTimesGrid from '../TideTimesGrid/TideTimesGrid';
import { withRouter } from 'react-router-dom';

class TideTimesLayout extends React.Component {

    siteNames = {
        "salcombe": "Salcombe",
        "newquay": "Newquay",
        "exmouth-dock": "Exmouth",
        "falmouth": "Falmouth"
    };

    state = {
        siteToDisplay: '',
        siteDate: {}
    }

    componentDidMount() {
        this.setState({ siteData: this.props.siteData, siteToDisplay: Object.keys(this.props.siteData)[0] });
    }

    componentWillReceiveProps(newProps) {
        this.setState({ siteData: newProps.siteData, siteToDisplay: Object.keys(newProps.siteData)[0] });
    }

    handleTabChange = (event, site) => {
        //this.setState({ siteToDisplay: site });
        this.props.history.push("/?siteId=" + site);
    };

    render() {

        const { siteData, siteToDisplay } = this.state;

        if (!siteData) {
            return <Typography variant="h3">No Data</Typography>;
        }

        return (
            <div>
                <Tabs value={siteToDisplay} onChange={this.handleTabChange} indicatorColor="primary"
            textColor="primary"
            fullWidth>
                    {
                        Object.keys(this.siteNames).map((key, index) => {
                            return (<Tab label={this.siteNames[key]} key={key} value={key}/>);
                        })
                    }
                </Tabs>
                {
                    (siteToDisplay === '')
                        ? <div>Nothing to see</div>
                        : <TideTimesGrid data={siteData[siteToDisplay]} />
                }
            </div>
        );
    }

}

export default withRouter(TideTimesLayout);